import React from 'react'
import { Link } from 'react-router-dom'
import { telegramIcon, whatsappIcon } from 'src/assets'
import { scrollToSection } from 'src/helpers'
import './Footer.css'

export const Footer = () => {
  return (
    <footer className='footer'>
      <div className="footer__wrapper">
        <div className='footer__navigation'>
          <a href='#person' onClick={() => scrollToSection('person')}></a>
          <a href='#experience' onClick={() => scrollToSection('experience')}> </a>
          <a href='#faq' onClick={() => scrollToSection('faq')} ></a>
          <Link to='/posts'> </Link>
          <a href='#blog' onClick={() => scrollToSection('blog')} ></a>
          <a href='#prices' onClick={() => scrollToSection('prices')}></a>
          <a href='#contacts' onClick={() => scrollToSection('contacts')}></a>
          <Link to='/documentation'></Link>
        </div>
        <div className='footer__text'>
          <p> </p>
          <p>2024 .</p>
        </div>
      </div>
    </footer>
  )
}
